export type AiQuestion = {
  id?: string;
  type: 'ox' | 'multiple' | 'short';
  question: string;
  options?: string[];
  correctAnswer: string | number;
  score?: number;
};

const KEY_STORAGE = 'gemini_api_key';

export const setGeminiKey = (k: string) => {
  localStorage.setItem(KEY_STORAGE, (k || '').trim());
};

export const getGeminiKey = (): string => {
  return localStorage.getItem(KEY_STORAGE) || '';
};

export async function generateQuestionsWithGemini(prompt: string, count = 10): Promise<AiQuestion[]> {
  const apiKey = getGeminiKey();
  if (!apiKey) throw new Error('Gemini API  .');

  const model = 'gemini-1.5-flash';

  // 2022   + ()   +  
  const sys = `:   "2022  "    .
:
-  "2022  " /   .
-  (///)       .
- ""    "(Decimal, )" . "(Prime)"   '(: prime)'   .
-   JSON  (//  ).
-   { "type":"ox|multiple|short", "question":"...", "options":[...], "correctAnswer":"...|index", "score":10 } .
  * multiple options , correctAnswer 0   index
  * ox options  correctAnswer "O"  "X"
  * short correctAnswer  
-   /  
-  length=${count}  .`;

  const user = ` : ${prompt}\n : ${count}\n :  JSON  `;

  const responseSchema = {
    type: 'ARRAY',
    items: {
      type: 'OBJECT',
      properties: {
        id: { type: 'STRING' },
        type: { type: 'STRING' },
        question: { type: 'STRING' },
        options: { type: 'ARRAY', items: { type: 'STRING' } },
        correctAnswer: { type: 'STRING' },
        score: { type: 'NUMBER' }
      },
      required: ['type', 'question', 'correctAnswer']
    }
  } as any;

  const body = {
    contents: [
      { role: 'user', parts: [{ text: sys }] },
      { role: 'user', parts: [{ text: user }] }
    ],
    generationConfig: {
      responseMimeType: 'application/json',
      responseSchema
    }
  } as any;

  console.info('[AI_GEN]   ', { model, count, prompt });

  const res = await fetch(`https://generativelanguage.googleapis.com/v1beta/models/${model}:generateContent?key=${encodeURIComponent(apiKey)}` , {
    method: 'POST',
    headers: { 'Content-Type':'application/json' },
    body: JSON.stringify(body),
  });
  if (!res.ok) {
    console.warn('[AI_GEN]   ', { status: res.status, statusText: res.statusText });
    throw new Error('Gemini  ');
  }
  const data = await res.json();

  let text: string = data?.candidates?.[0]?.content?.parts?.[0]?.text || '';
  console.info('[AI_GEN]   ', { length: text.length, preview: text.slice(0, 300) });
  // code fence 
  if (text.startsWith('```')) {
    const first = text.indexOf('\n');
    const lastFence = text.lastIndexOf('```');
    if (first !== -1 && lastFence !== -1) text = text.slice(first + 1, lastFence);
  }
  let parsed: any;
  try {
    parsed = JSON.parse(text);
  } catch {
    console.warn('[AI_GEN]  JSON  ( )');
    const jsonStart = text.indexOf('[');
    const jsonEnd = text.lastIndexOf(']');
    if (jsonStart === -1 || jsonEnd === -1) throw new Error('AI   ');
    parsed = JSON.parse(text.slice(jsonStart, jsonEnd + 1));
  }
  const arr: any[] = Array.isArray(parsed) ? parsed : [parsed];
  console.info('[AI_GEN]   ', { returned: arr.length });
  const isValidItem = (q: any) => q && ['ox','multiple','short'].includes(q.type) && typeof q.question === 'string' && (q.type !== 'multiple' || Array.isArray(q.options));
  let items = arr.filter(isValidItem);
  console.info('[AI_GEN]    ', { valid: items.length, requested: count });
  if (items.length > count) items = items.slice(0, count);
  if (items.length < count) {
    console.warn('[AI_GEN]   ', { valid: items.length, requested: count });
    throw new Error(`AI ${items.length} .       .`);
  }

  return items.map((q, i) => ({
    id: q.id || `ai_${Date.now()}_${i}`,
    type: q.type,
    question: q.question,
    options: q.options,
    correctAnswer: q.correctAnswer,
    score: typeof q.score === 'number' ? q.score : 10,
  }));
}


