import React, { useEffect, useState } from 'react';
import { generateQuestionsWithGemini, getGeminiKey, setGeminiKey, AiQuestion } from '../services/ai';

interface Props {
  onClose: () => void;
  onGenerate: (questions: AiQuestion[]) => void;
}

const AiQuestionModal: React.FC<Props> = ({ onClose, onGenerate }) => {
  const [apiKey, setApiKey] = useState<string>('');
  const [prompt, setPrompt] = useState<string>('');
  const [count, setCount] = useState<number>(10);
  const [saving, setSaving] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string>('');
  const [savedOnce, setSavedOnce] = useState<boolean>(false);

  useEffect(() => {
    setApiKey(getGeminiKey());
  }, []);

  const handleSaveKey = async () => {
    try {
      setSaving(true);
      setGeminiKey(apiKey);
      setSavedOnce(true);
    } catch (e: any) {
      setError(e?.message || 'API   ');
    } finally {
      setSaving(false);
    }
  };

  const handleGenerate = async () => {
    setError('');
    if (!prompt.trim()) {
      setError(' .');
      return;
    }
    try {
      setLoading(true);
      console.info('[AI_GEN_UI]  ', { count, prompt });
      const list = await generateQuestionsWithGemini(prompt.trim(), count);
      console.info('[AI_GEN_UI]  ', { received: list.length });
      onGenerate(list);
    } catch (e: any) {
      console.warn('[AI_GEN_UI]  ', { message: e?.message });
      setError(e?.message || 'AI   ');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="modal-backdrop">
      <div className="modal">
        <div className="modal-header">
          <h3>AI  </h3>
          <button className="close-btn" onClick={onClose}></button>
        </div>
        <div className="modal-body">
          <div className="form-group">
            <label>Gemini API </label>
            <input
              type="text"
              value={apiKey}
              onChange={(e) => setApiKey(e.target.value)}
              placeholder="AIza..."
            />
            <button onClick={handleSaveKey} disabled={saving}>
              {saving ? ' ...' : ' '}
            </button>
            {savedOnce && <small> .  .</small>}
          </div>

          <div className="form-group">
            <label></label>
            <textarea
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
              rows={4}
              placeholder=": 4   10 "
            />
          </div>

          <div className="form-group">
            <label> </label>
            <input
              type="number"
              min={1}
              max={50}
              value={count}
              onChange={(e) => setCount(parseInt(e.target.value) || 1)}
            />
          </div>

          {error && <div className="error-text">{error}</div>}
        </div>
        <div className="modal-footer">
          <button className="btn-secondary" onClick={onClose}></button>
          <button className="btn-primary" onClick={handleGenerate} disabled={loading}>
            {loading ? ' ...' : 'AI '}
          </button>
        </div>
      </div>
    </div>
  );
};

export default AiQuestionModal;


